* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding-top: 4rem;
  color: #333;
  --type-first: Helvetica, Arial, sans-serif;
  --type-second: 'Spectral', georgia;
  font-family: var(--type-first);
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
}

button,
input {
  display: block;
  font-size: 1rem;
  font-family: var(--type-first);
  color: #333;
}

.container {
  max-width: 50rem;
  padding: 0 1rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #333;
}

.animeLeft {
  opacity: 0;
  transform: translateX(-20px);
  animation: animeLeft 0.5s forwards;
}

@keyframes animeLeft {
  to {
    opacity: 1;
    transform: none;
    transform: initial;
  }
}

.title {
  font-size: 3rem;
  margin: 1rem 0;
  position: relative;
  line-height: 1;
  font-family: var(--type-second);
}

.title::after {
  content: '';
  display: block;
  width: 1.5rem;
  background: #fb1;
  height: 1.5rem;
  position: absolute;
  bottom: 5px;
  left: -5px;
  border-radius: 0.2rem;
  z-index: -1;
}

.mainContainer {
  margin-top: 2rem;
}

.appBody {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh + 10rem);
}

.VictoryContainer {
  height: auto !important;
  height: initial !important;
}


